import React from 'react';
import './ProjectCard.css';

function ProjectCard({ project }: Props) {
    const Fade = require('react-reveal/Fade');
    return (
        <Fade duration={800} bottom>
        <div className="project-card-main">
            <div className="card-title">
                <p>{project.title}</p>
            </div>
            <div className='card-image'>
                <img src={require(`../../assets/${project.image}`)} alt="Logo" />
            </div>
            <div className="card-desc">
                <p className="card-description">{project.desc}</p>
                <p className="card-duration">Í∞úÎ∞ú Í∏∞Í∞Ñ : {project.duration}</p>
                
                <div className="card-links">
                    {project.url !== "none" && <a className="card-git" href={project.url} target='_blank'>GitHub</a>}
                    {project.demo !== "none" && <a className="card-demo" href={project.demo} target='_blank'>Demo</a>}
                </div>

                <p className="card-skill">{project.skill}</p>
            </div>
        </div>
        </Fade>
    );
}
interface Props {
    project: {
        title: string,
        desc: string,
        image: string,
        duration: string,
        skill: string,
        url: string,
        demo: string
    }
}
export default ProjectCard;